export default {
	data() {
		return {
			inv: 0,
			type: 0, //类别
			loadMoreText: '加载中...',
			showLoadMore: false,
			isMore: true,
			PageNo: 1,
			PageSize: 10,
			mainItem: [],
			keyWords: '' //搜索关键词
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad(option) {
		this.initData();
		this.getUserUp();
	},
	onUnload() {
		(this.PageNo = 1), (this.mainItem = []), (this.loadMoreText = '加载更多'), (this.showLoadMore = false);
	},
	onReachBottom() {
		//下拉加载
		this.showLoadMore = true;
		setTimeout(() => {
			this.setListData();
		}, 300);
	},
	onPullDownRefresh() {
		//上拉刷新
		this.initData();
	},
	methods: {
		search(res) {
			this.keyWords = res.value;
			this.initData();
		},
		dumpNotice() {
			//跳转公告
			uni.navigateTo({
				url: '/pages/news/notice'
			});
		},
		getDate(item) { // 时间戳转日期
			var time = new Date(item)
			var year = time.getFullYear()
			var month = time.getMonth() + 1
			var date = time.getDate()
			var hours = time.getHours()
			var minutes = time.getMinutes()
			return year + '-' + this.add0(month) + '-' + this.add0(date) + " " + this.add0(hours) + ":" + this.add0(
				minutes)
		},
		add0(m) {
			return m < 10 ? '0' + m : m
		},
		dumpInfo(id) {
			uni.navigateTo({
				url: '/pages/news/info?id=' + id
			});
		},
		initData() {
			//刷新数据
			setTimeout(() => {
				this.PageNo = 1;
				this.mainItem = [];

				this.getPage();
				uni.stopPullDownRefresh();
			}, 300);
		},
		setListData() {
			//加载更多数据
			this.PageNo += 1;
			if (this.isMore) this.getPage();
		},
		getPage() {
			var _this = this;
			uni.showLoading({
				title: '数据查询中'
			});
			_this.$request.news
				.article({
					page: _this.PageNo
				})
				.then(data => {
					_this.$tools.loadingHide();
					if (data.status == 1) {
						_this.mainItem = _this.mainItem.concat(data.data);
						if (data.data.length < _this.PageSize) {
							this.loadMoreText = '没有更多数据了';
							_this.isMore = false;
						} else {
							_this.showLoadMore = false;
							_this.isMore = true;
						}
					} else {
						_this.showLoadMore = false;
						_this.$tools.toast(data.msg)
					}
				})
				.catch(err => {
					_this.showLoadMore = false;
					_this.$tools.loadingHide();
					//消息异常
					_this.$tools.toast('数据加载异常')
				});

		},
		getUserUp() {
			//获取是否可以升级
			var _this = this;
			_this.$request.user
				.getUserData({})
				.then(data => {
					if (data.status == 1) {
						if (data.data.up == 1) {
							uni.setTabBarBadge({
								index: 3,
								text: '1'
							})
						} else {
							uni.setTabBarBadge({
								index: 3
							})
						}
					}
				});
		}
	}
};
